import React from 'react';
import Board from "../Board/Board";
import './Game.css';

/**
 * Contains the game logic
 */
class Game extends React.Component {
  state = {
    history: [
      {
        squares: Array(9).fill(null)
      }
    ],
    stepNumber: 0,
    xIsNext: true
  };

  /**
   * Handle restart game
   */
  handleRestart = ()=>{
    this.setState({
      history: [
        {
          squares: Array(9).fill(null)
        }
      ],
      stepNumber: 0,
      xIsNext: true
    });
  }
  
    /**
     * Handle click of squares
     * @param {*} i 
     * @returns 
     */
    handleClick(i) {
      const history = this.state.history.slice(0, this.state.stepNumber + 1);
      const current = history[history.length - 1];
      const squares = current.squares.slice();
      if (calculateWinner(squares) || squares[i]) {
        return;
      }
      squares[i] = this.state.xIsNext ? "X" : "O";
      this.setState({
        history: history.concat([
          {
            squares: squares
          }
        ]),
        stepNumber: history.length,
        xIsNext: !this.state.xIsNext
      });
    }

    /**
     * Get the move element
     * @returns 
     */
    getMoves(history){
      const moves = history.map((step, move) => {
        const desc = move ?
          'Go to move #' + move :
          'Go to game start';
        return (
          <li key={move}>
            <button className="btn-history" onClick={() => this.jumpTo(move)}>{desc}</button>
          </li>
        );
      });
      return moves;
    }
  
    /**
     * Jump to movement
     * @param {*} step 
     */
    jumpTo(step) {
      this.setState({
        stepNumber: step,
        xIsNext: (step % 2) === 0
      });
    }
  
    /**
     * Get color of message
     */
    getColorMessage(winner){
      if(winner){
        return winner === 'X'?'font-pink':'font-green';
      }
      return ''
    }

    render() {
      const history = this.state.history;
      const current = history[this.state.stepNumber];
      const winner = calculateWinner(current.squares);
      const moves = this.getMoves(history);  
      let status;
      if (winner) {
        status = "Winner: " + winner;
      } else {
        status = "Next player: " + (this.state.xIsNext ? "X" : "O");
      }
  
      return (
        <div className="game-container">
          <div className="game-actions">
          <button className="restart-button" onClick={this.handleRestart} > Restart </button> 
          </div>
          <div className="game">  
            <div className="game-board">
              <Board
                squares={current.squares}
                onClick={i => this.handleClick(i)}
              />
            </div>
            <div className="game-info">
              <h2>History</h2>
              <ol>{moves}</ol>
            </div>
        </div>
        <div className="game-messages">
          <p className={this.getColorMessage(winner)}>{status}</p>
        </div>
        </div>
      );
    }
  }

  
  function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6]
    ];
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        return squares[a];
      }
    }
    return null;
  }

  
export default Game;